*{box-sizing:border-box;font-family:"Inter",sans-serif;font-size:14px;margin:0;padding:0}body{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.light{--bg: white;--bg-top: #f0f2fa;--card-bg: #f0f2fa;--text-primary: #1e202a;--text-neutral: #63687e}.dark{--bg: #1e202a;--bg-top: #252a41;--card-bg: #252a41;--text-primary: white;--text-neutral: #8b97c6}.app{position:absolute;width:100vw;height:auto;background-color:var(--bg);z-index:-1}body{background:var(--bg)}.main-bg{position:absolute;border-radius:0 0 25px 25px;overflow:hidden;width:100vw;height:15rem;top:0;z-index:-1;background-color:var(--bg-top)}.wrapper{margin:0 10%}header{display:flex;justify-content:space-between;align-items:center;margin:2rem 0}@media screen and (max-width: 600px){header{align-items:unset;flex-direction:column}}.main-header-container h1,.main-header-container p{line-height:1.3}.main-header-container h1{font-size:clamp(1.4rem, 0.3rem + 3.85vw, 2.5rem);color:var(--text-primary)}.main-header-container p{font-size:clamp(0.84rem, 0.41rem + 1.505vw, 1.27rem);font-weight:700;color:var(--text-neutral)}.divider{display:none}@media screen and (max-width: 600px){.divider{display:block;margin:10px 0;border-top:solid 2px #63687e}}.toggle-container{display:flex;justify-content:flex-end;align-items:center}.toggle-container>*{margin:0 10px}@media screen and (max-width: 600px){.toggle-container{justify-content:space-between}.toggle-container>*{margin:0}}.toggle-container p{font-weight:700;font-size:clamp(0.8rem, 0.6rem + 0.7vw, 1rem);color:var(--text-neutral)}.overall-stats,.detail-stats{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem;grid-row-gap:2rem;row-gap:2rem;margin:2.5rem 0}h2{font-size:2.2rem;margin:1rem 0;color:var(--text-primary)}footer{color:var(--text-primary)}
.increase{color:#1db489}.decrease{color:#dc414c}.no-change{color:#63687e}.overall-stats__item{display:flex;flex-direction:column;background-color:var(--card-bg);text-align:center;line-height:1.6}.overall-stats__item>div:first-child{margin:1rem 0;display:flex;align-items:center;justify-content:center;color:var(--text-neutral)}.overall-stats__item>div:first-child p{margin-left:7px;font-weight:700}.overall-stats__item>p:nth-child(2){line-height:normal;font-size:3.5rem;font-weight:700;color:var(--text-primary)}.overall-stats__item>p:nth-child(3){text-transform:uppercase;color:#63687e;letter-spacing:5px}.overall-stats__item>div:last-child{display:flex;align-items:center;justify-content:center;margin:1rem 0;font-weight:700}.overall-stats__item>div:last-child p{margin-left:5px}
.increase{color:#1db489}.decrease{color:#dc414c}.no-change{color:#63687e}.detail-stats__item{display:grid;grid-template-areas:"heading . icon" "number . percent";grid-row-gap:1.25rem;row-gap:1.25rem;padding:1.5rem 1.75rem;justify-content:space-between;align-content:space-between;background:var(--card-bg);border-radius:5px}.detail-stats__item div:first-child{grid-area:heading;color:var(--text-neutral);font-size:1.25rem;font-weight:700;text-transform:capitalize}.detail-stats__item div:nth-child(2){grid-area:icon}.detail-stats__item div:nth-child(3){grid-area:number}.detail-stats__item div:nth-child(3) p{font-weight:700;font-size:2.5rem;color:var(--text-primary)}.detail-stats__item div:last-child{grid-area:percent;display:flex;align-items:flex-end;font-weight:700}.detail-stats__item div:last-child svg{vertical-align:text-bottom}.icon{text-align:end}
.toggle-container__the-toggle{position:relative}.toggle-container__the-toggle label{width:65px;height:30px;background:#aeb3cb;display:inline-block;border-radius:46px;transition:200ms;cursor:pointer}.toggle-container__the-toggle label::after{content:"";position:absolute;height:23px;width:23px;right:5px;top:3.5px;border-radius:100%;background:var(--bg-top);transition:200ms;z-index:2}.toggle-container__the-toggle input{display:none}.toggle-container__the-toggle input:checked+label{background:linear-gradient(to right, #378fe6, #3eda82)}.toggle-container__the-toggle input:checked+label::after{right:37.5px}
